@import '../../../../asset/style/variable';

.filterInput {
  padding-right: 50px;
}

.scrollWrap {
  padding-left: 1.5rem;
}

.scrollContent {
  padding-right: 1.5rem;
}

.item {
  display: grid;
  grid-template-columns: auto minmax(auto, 1fr) minmax(0px, 30px);
  gap: 12px;
  justify-content: space-between;
  align-items: center;
  height: 57px;
  padding: 4px 24px;
  cursor: pointer;
  &:hover {
    background-color: $raven;
  }
}

.logo {
  width: 24px;
  height: 24px;
  border-radius: $border-radius;
}

.symbol {
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
}

.name {
  max-width: 360px;
  font-size: $font-size-md;
  font-weight: $font-weight-xl;
  color: $stone;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.note {
  font-size: $font-size-sm;
  font-weight: $font-weight-lg;
  color: $stone;
}

.disable {
  cursor: inherit;
  &:hover {
    background: $shadow;
  }
  .logo {
    opacity: $opacity-blur;
  }
  .symbol {
    color: $stone;
  }
}
