@import '../../../../asset/style/variable';

.checkbox {
  cursor: pointer;

  &:hover {
    .checkmark {
      border-color: $stone;
    }
  }
}

.checkcontrol {
  display: none;
  &:checked {
    & ~ .checkmark {
      border-color: $blue;
      &:after {
        display: block;
      }
    }
  }
}

.checkmark {
  position: relative;
  height: 24px;
  width: 24px;
  border: 2px solid $slate;
  border-radius: $border-radius;
  background-color: transparent;
  &:after {
    content: '';
    display: none;
    position: absolute;
    top: 2px;
    left: 7px;
    width: 7px;
    height: 12px;
    border: solid $blue;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
  }
}

.rightTitle {
  margin-left: 4px;
}

.leftTitle {
  margin-right: 4px;
}
