@import '../../../asset/style/variable';

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 36px;
  margin: 12px 0;
  border: 2px solid $blue-dark;
  border-radius: $border-radius;
  vertical-align: middle;
  font-size: $font-size-xl;
  color: $blue-dark;
  background-color: transparent;

  &:hover {
    border-color: $blue;
    color: $blue;
  }
}
