@import '../../../../asset/style/variable';

.wrap {
  width: 100%;
  padding: 16px 20px;
  border-radius: $border-radius;
  background-color: $raven;
}

.input {
  width: 100%;
  height: 30px;
  padding: 0px;
  border: none;
  outline: none;
  text-align: right;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
  color: $snow;
  background-color: transparent;
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 30px;
  line-height: $font-height-default;
}

.max {
  padding: 3px 7px 4px;
  border: 2px solid $blue-dark;
  border-radius: 20px;
  font-size: $font-size-xs;
  font-weight: $font-weight-xl;
  color: $blue-dark;
  background-color: transparent;
  &:hover {
    border-color: $blue;
    color: $blue;
  }
}
