@import '../../../asset/style/variable';

.dropdownToggle {
  border: none;
  font-size: $font-size-xxl;
  font-weight: $font-weight-xl;
  color: $snow;
  background: transparent;
  opacity: $opacity-blur;
  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none;
    border: none;
    color: $snow;
    background: transparent;
    box-shadow: none;
    outline: none;
    opacity: $opacity-default;
  }

  .logo {
    width: 36px;
    height: 36px;
  }
}

.dropdownMenu {
  border-radius: $border-radius;
  background-color: $raven;
}

.dropdownItem {
  font-size: $font-size-xl;
  font-weight: $font-weight-xl;
  color: $snow;
  &:hover {
    color: $snow;
    background-color: $raven;
  }
}

.logo {
  width: 24px;
  height: 24px;
  margin-right: 6px;
}

.title {
  margin-top: 6px;
}
