@import '../../../../asset/style/variable';

.selectBtn {
  border: none;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
  color: $snow;
  background: transparent;
  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none;
    border: none;
    color: $snow;
    background: transparent;
    box-shadow: none;
    outline: none;
    .iconDropdown {
      color: $snow;
    }
  }
}

.logo {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  border-radius: $border-radius;
}

.symbol {
  margin-right: 4px;
}

.iconDropdown {
  color: $stone;
  &::before {
    font-weight: $font-weight-xl !important;
  }
}
